"use client";

import React from "react";
import { Play, Calendar, Eye, Clock, Share2, MoreHorizontal, ChevronLeft, CheckCircle2 } from "lucide-react";
import ReactPlayer from "react-player";
import {useHighlightBySlug} from "../../src/hook/useHighlightBySlug";

const formatDate = (dateString: string) =>
    dateString ? new Date(dateString).toLocaleDateString("en-US", { year: "numeric", month: "long", day: "numeric" }) : "";

const formatViews = (num: number) => {
    if (!num) return "0";
    if (num >= 1000000) return (num / 1000000).toFixed(1) + "M";
    if (num >= 1000) return (num / 1000).toFixed(1) + "k";
    return num.toString();
};

export default async function VideoDetailPage({ params }: { params: { slug: string } }) {
    const video: Video | null = await fetchHighlightBySlug(params.slug);

    if (!video) return <div className="text-slate-900 p-5">Video not found</div>;

    return (
        <div className="min-h-screen bg-[#F8F9FA] text-slate-900 pb-12">

            {/* Navigation / Breadcrumb */}
            <div className="bg-white border-b border-slate-200 sticky top-0 z-30">
                <div className="max-w-7xl mx-auto px-4 md:px-8 h-16 flex items-center justify-between">
                    <a href="/highlights" className="flex items-center gap-2 text-slate-600 hover:text-[#483D67] font-medium transition-colors">
                        <ChevronLeft className="w-5 h-5" /> Back to Highlights
                    </a>
                    <div className="flex items-center gap-4">
                        <button className="p-2 hover:bg-slate-100 rounded-full text-slate-500"><Share2 className="w-5 h-5"/></button>
                        <button className="p-2 hover:bg-slate-100 rounded-full text-slate-500"><MoreHorizontal className="w-5 h-5"/></button>
                    </div>
                </div>
            </div>

            <main className="max-w-5xl mx-auto px-4 md:px-8 py-8">

                {/* Video Player */}
                <div className="relative aspect-video bg-black rounded-2xl overflow-hidden shadow-xl">
                    <ReactPlayer
                        src={video.video_url}
                        controls
                        width="100%"
                        height="100%"
                        className="rounded-2xl"
                    />
                </div>

                {/* Metadata */}
                <div className="mt-6">
                    <div className="flex flex-wrap items-center gap-3 mb-3">
                        {video.category && (
                            <span className="bg-[#483D67] text-white text-xs font-bold px-3 py-1 rounded-full uppercase tracking-wider">
                {video.category.name}
              </span>
                        )}
                        <span className="text-slate-500 text-sm flex items-center gap-1">
              <Clock className="w-4 h-4" /> 2 hours ago
            </span>
                    </div>

                    <h1 className="text-2xl md:text-4xl font-bold text-slate-900 leading-tight mb-4">
                        {video.title}
                    </h1>

                    <div className="flex items-center justify-between border-b border-slate-200 pb-6">
                        <div className="flex items-center gap-6 text-sm text-slate-500 font-medium">
              <span className="flex items-center gap-2">
                <Eye className="w-5 h-5 text-slate-400" /> {formatViews(video.views_count)} views
              </span>
                            <span className="flex items-center gap-2">
                <Calendar className="w-5 h-5 text-slate-400" /> {formatDate(video.published_at)}
              </span>
                        </div>
                    </div>
                </div>

                {/* Author & Description */}
                <div className="bg-white p-6 rounded-2xl border border-slate-200 shadow-sm mt-6">
                    <div className="flex items-start gap-4">
                        <div className="w-12 h-12 rounded-full bg-slate-200 overflow-hidden flex-shrink-0">
                            {video.author?.avatar_url ? (
                                <img src={video.author.avatar_url} alt={video.author.name} className="w-full h-full object-cover" />
                            ) : (
                                <div className="w-full h-full bg-[#483D67] flex items-center justify-center text-white font-bold text-lg">
                                    {video.author?.name?.[0] || 'A'}
                                </div>
                            )}
                        </div>
                        <div className="flex-1">
                            <div className="flex justify-between items-start mb-2">
                                <div>
                                    <h3 className="font-bold text-slate-900 text-lg flex items-center gap-2">
                                        {video.author?.name || "Unknown Author"}
                                        <CheckCircle2 className="w-4 h-4 text-blue-500 fill-current" />
                                    </h3>
                                    <p className="text-slate-500 text-sm">{video.author?.role || "Contributor"} â€¢ 245 Followers</p>
                                </div>
                            </div>
                            <div className="text-slate-600 leading-relaxed mt-4 space-y-4">
                                <p>{video.summary}</p>
                                {video.content && <p>{video.content}</p>}
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </div>
    );
}
